
    import { defineComponent , toRef } from "vue";
 
    export default defineComponent({
          name: "SectionTitleFrame",
          props: { "title_name" : 
                    { type : String }
          },
          setup (props ) {
          const title_name_ref = toRef(props , "title_name" )
          return{
            title_name_ref
          }
          },

    
    });
