
  import { defineComponent } from "vue";

  import router from "../router";

  
export default defineComponent({
  name: "SimpleFooter",
    components: {    },
    setup(){
      const BtnData = router.options.routes
      const GoToTop = () =>{
        window.scrollTo({
          top: 0,
          behavior: "smooth",
        });  
      }
      return {BtnData , GoToTop}
    }
  });
